<template>
    <ac-layout>
        <ac-view class="page-body">
            <ac-view class="page-section page-section-gap">
                <ac-view class="page-section-title">Local Image</ac-view>
                <ac-view class="page-section-ctn">
                    <ac-image class="image" :src="jpg1"/>
                </ac-view>
            </ac-view>
            <ac-view class="page-section page-section-gap">
                <ac-view class="page-section-title">Internet Image</ac-view>
                <ac-view class="page-section-ctn">
                    <ac-image class="image" :src="jpg2"/>
                </ac-view>
            </ac-view>
        </ac-view>
    </ac-layout>
</template>

<script>
    export default {
        config: {
            navigationBarTitleText: '图片'
        },
        name: 'image',
        data() {
            return {
                jpg1: 'https://ss1.baidu.com/6ONXsjip0QIZ8tyhnq/it/u=899324191,1495050524&fm=173&app=25&f=JPEG?w=639&h=426&s=07D15A824FFF9ADC0251B89503001080',
                jpg2: 'https://ss1.baidu.com/6ONXsjip0QIZ8tyhnq/it/u=2075656774,2708770842&fm=173&app=25&f=JPEG?w=639&h=458&s=2F415B6E5BB1A6595CDC150B0000E0C1'
            }
        },
        methods: {}
    }
</script>

<style lang="less">
    .page-section-ctn {
        text-align: center;

    }

    .page-section-gap {
        box-sizing: border-box;
        padding: 0 1.5rem;

    }

    .page-section {
        width: 100%;
        margin-bottom: 2rem;

    }

    .image {
        display: inline-block;
        overflow: hidden;
        margin-top: 1.5rem;

    }

</style>



