<template>
    <ac-layout>
        <ac-view class="win">微应用ID</ac-view>
        <ac-block class="input-demo">
            <ac-input placeholder="请输入微应用ID" v-model="applicationID"></ac-input>
        </ac-block>
        <!--<ac-view class="win">微应用参数</ac-view>-->
        <!--<ac-block class="input-demo">-->
            <!--<ac-input placeholder="请输入微应用参数" v-model="applicationParams"></ac-input>-->
        <!--</ac-block>-->
        <!--<ac-view class="win">授权code</ac-view>-->
        <!--<ac-block class="input-demo">-->
            <!--<ac-input placeholder="请输入授权code" v-model="applicationCode"></ac-input>-->
        <!--</ac-block>-->
        <ac-button @tap="callApplication" type="primary">调用1</ac-button>
    </ac-layout>
</template>

<script type="text/babel">
  import ACTopText from './ACTopText'

  export default {
    config: {
      navigationBarTitleText: '调用微应用'
    },
    components: {ACTopText},
    data() {
      return {
        applicationID: 'd61896f70c7f4cf699c9179504a2c173',
        applicationParams: '',
        applicationCode: '',
      }
    },
    methods: {
      callApplication() {
        let o = {
          microAppUrl: 'http://192.168.89.52:8008/C0000000000003.zip',
          microAppId: this.applicationID,
          microAppParams: this.applicationParams,
          microAppCode: this.applicationCode,
        }
        appcan.openMicroApp(o);
      }
    }
  }
</script>
<style lang="less">
    .ac-content .win {
        padding: 10px;
        color: #999;
    }

    .input-demo {
        display: -webkit-flex;
        display: flex;
        -webkit-align-items: center;
        align-items: center;
        width: 100%;
        border-bottom: 1px solid #ccc;
        border-top: 1px solid #ccc;
        background-color: #fff;
        padding: 4px 12px;
    }

</style>



