<template>
  <div class="audio-wrapper">
    <audio id="audio">
      <source :src="current.src" type="audio/mp3">
    </audio>
    <div class="audio-left"><img id="audioPlayer" @click="audioPlayer" :src="suspend"></div>
    <div class="audio-right">
      <p style="max-width: 536px;">{{current.name}}——{{current.author}}</p>
      <div class="progress-bar-bg" id="progressBarBg"><span id="progressDot" :style="{width:progressDotW}"></span>
        <div class="progress-bar" id="progressBar" :style="{width:progressBarW}"></div>
      </div>
      <div class="audio-time"><span class="audio-length-current" id="audioCurTime">{{audioCurTime}}</span><span class="audio-length-total"></span></div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'ac-audio',
    props:{
      current:{
        type: Object
      }
    },
    data () {
      return {
        suspend:'http://imgsrc.baidu.com/imgad/pic/item/d8f9d72a6059252d714089383f9b033b5bb5b9b3.jpg',
        progressBarW:'',
        progressDotW:'',
        audioCurTime:'00:00',
      }
    },
    methods: {
      //播放暂停
      audioPlayer(){
        const audioL = document.getElementById('audio');
        audio.addEventListener('timeupdate',  ()=> {
          this.updateProgress(audio);
        }, false);
        audio.addEventListener('ended',  () =>{
          this.audioEnded();
        }, false);
        if (audioL.paused) {
          // 开始播放当前点击的音频
          audioL.play();
          this.suspend = "http://imgsrc.baidu.com/imgad/pic/item/a686c9177f3e6709043b960c31c79f3df8dc550e.jpg";
        } else {
          audioL.pause();
          this.suspend = "http://imgsrc.baidu.com/imgad/pic/item/d8f9d72a6059252d714089383f9b033b5bb5b9b3.jpg";
        }
      },
      updateProgress(audio){
        // const audio = document.getElementById('audio');
        var value = audio.currentTime / audio.duration;
        this.progressBarW = value * 100 + '%';
        this.progressDotW = value * 100 + '%';
        this.audioCurTime = this.transTime(audio.currentTime);
      },
      //更新进度条与当前播放时间
      transTime(value){
        var time = "";
        var h = parseInt(value / 3600);
        value %= 3600;
        var m = parseInt(value / 60);
        var s = parseInt(value % 60);
        if (h > 0) {
          time = this.formatTime(h + ":" + m + ":" + s);
        } else {
          time = this.formatTime(m + ":" + s);
        }

        return time;
      },
      // 格式化时间显示，补零对齐
      formatTime(value){
        var time = "";
        var s = value.split(':');
        var i = 0;
        for (; i < s.length - 1; i++) {
          time += s[i].length == 1 ? ("0" + s[i]) : s[i];
          time += ":";
        }
        time += s[i].length == 1 ? ("0" + s[i]) : s[i];

        return time;
      },
      //播放完成  进度重置
      audioEnded(){
        this.progressBarW = 0;
        this.progressDotW = 0;
        this.suspend = "http://imgsrc.baidu.com/imgad/pic/item/d8f9d72a6059252d714089383f9b033b5bb5b9b3.jpg";
      },
    },
    mounted () {
      const audio = document.getElementById('audio');
      let that = this;

      const dot = document.getElementById('progressDot');
      dot.onmousedown = function (e) {
        if (!audio.paused || audio.currentTime != 0) {
          var oriLeft = dot.offsetLeft;
          var mouseX = e.clientX;
          var maxLeft = oriLeft; // 向左最大可拖动距离
          var maxRight = document.getElementById('progressBarBg').offsetWidth - oriLeft; // 向右最大可拖动距离

          // 禁止默认的选中事件（避免鼠标拖拽进度点的时候选中文字）
          if (e.preventDefault) {
            e.preventDefault();
          } else {
            e.returnValue = false;
          }

          // 禁止事件冒泡
          if (e && e.stopPropagation) {
            e.stopPropagation();
          } else {
            window.event.cancelBubble = true;
          }

          // 开始拖动
          document.onmousemove = function (e) {
            var length = e.clientX - mouseX;
            if (length > maxRight) {
              length = maxRight;
            } else if (length < -maxLeft) {
              length = -maxLeft;
            }
            var pgsWidth = document.getElementById('progressBarBg').offsetWidth;
            var rate = (oriLeft + length) / pgsWidth;
            audio.currentTime = audio.duration * rate;
            that.updateProgress(audio);
          };

          // 拖动结束
          document.onmouseup = function () {
            document.onmousemove = null;
            document.onmouseup = null;
          };
        }
      };
    }
  }
</script>

<style lang="less" scoped>
  .audio-wrapper {
    background-color: #fcfcfc;
    margin: 20rpx auto;
    max-width: 1340rpx;
    height: 140rpx;
    border: 1px solid #e0e0e0;
  }

  .audio-left {
    float: left;
    text-align: center;
    width: 18%;
    height: 100%;
  }

  .audio-left img {
    width: 80rpx;
    position: relative;
    top: 30rpx;
    margin: 0;
    display: initial;   /* 解除与app的样式冲突 */
    cursor: pointer;
  }

  .audio-right {
    margin-right: 2%;
    float: right;
    width: 80%;
    height: 100%;
  }

  .audio-right p {
    font-size: .75rem;
    height: 35%;
    margin: 16rpx 0;

    /* 歌曲名称只显示在一行，超出部分显示为省略号 */
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    max-width: 486rpx;
  }

  .progress-bar-bg {
    background-color: #d9d9d9;
    position: relative;
    height: 4rpx;
    cursor: pointer;
  }

  .progress-bar {
    background-color: #649fec;
    width: 0;
    height: 4rpx;
  }

  .progress-bar-bg span {
    content: " ";
    width: 20rpx;
    height: 20rpx;
    border-radius: 50%;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    background-color: #3e87e8;
    position: absolute;
    left: 0;
    top: 50%;
    margin-top: -10rpx;
    margin-left: -10rpx;
    cursor: pointer;
  }

  .audio-time {
    overflow: hidden;
    margin-top: -1px;
  }

  .audio-length-total {
    float: right;
    font-size: .6rem;
    margin-top: 0.3rem;
  }

  .audio-length-current {
    float: left;
    font-size: .6rem;
    margin-top: 0.3rem;
  }
</style>



