<template>
    <ac-layout>
        <ac-view>
            <ACTopText :text="title"></ACTopText>
            <ac-view class="page-body">
                <ac-view class="page-section">
                    <ac-view class="page-section-title">输入区域高度自适应，不会出现滚动条</ac-view>
                    <ac-view>
                        <ac-textarea auto-height placeholder="蓝色placeholder"
                                     placeholder-class="blue-placeholder"></ac-textarea>
                    </ac-view>
                    <!--<ac-button @tap="a">aaaa</ac-button>-->
                </ac-view>
            </ac-view>
        </ac-view>
    </ac-layout>

</template>

<script type="text/babel">
  import ACTopText from './ACTopText';
  import {globalBus} from './globalBus';

  export default {
    components: {ACTopText},
    config: {
      navigationBarTitleText: 'Textarea'
    },
    data() {
      return {
        title: 'Textarea'
      }
    },
    methods: {
      a() {
        globalBus.$on('countNumber', (number) => {
          alert(`brother 被点击了 ${number} 次。`);
        });
      }
    },
  }
</script>
<style lang="less">
    @import '../../common/app.less';
    @import '../../common/common.less';

    .blue-placeholder::-webkit-input-placeholder {
        color: rgb(58, 47, 207);
    }

    .ac-textarea {
        border: none;
    }
</style>



