<template>
    <ac-layout>

        <ACTopText :text="title"></ACTopText>

        <ac-view class="icon-box">
            <ac-icon :name="'checkoff'" size="4rem" color="#04dc12"></ac-icon>
            <ac-view class="icon-box-ctn">
                <ac-view class="icon-box-title">成功</ac-view>
                <ac-view class="icon-box-desc">用于表示操作顺利完成</ac-view>
            </ac-view>
        </ac-view>


        <ac-view class="icon-box">
            <ac-icon name="warn" size="4rem" color="#00acff"></ac-icon>
            <ac-view class="icon-box-ctn">
                <ac-view class="icon-box-title">提示</ac-view>
                <ac-view class="icon-box-desc">用于表示信息提示；也常用于缺乏条件的操作拦截，提示用户所需信息</ac-view>
            </ac-view>
        </ac-view>


        <ac-view class="icon-box">
            <ac-icon :name="'warn'" size="4rem" color="#c9c9c9"></ac-icon>
            <ac-view class="icon-box-ctn">
                <ac-view class="icon-box-title">普通警告</ac-view>
                <ac-view class="icon-box-desc">用于表示操作后将引起一定后果的情况；也用于表示由于系统原因而造成的负向结果</ac-view>
            </ac-view>
        </ac-view>

        <ac-view class="icon-box">
            <ac-icon :name="'warn'" size="4rem" color="#f9615c"></ac-icon>
            <ac-view class="icon-box-ctn">
                <ac-view class="icon-box-title">强烈警告</ac-view>
                <ac-view class="icon-box-desc">用于表示由于用户原因造成的负向结果；也用于表示操作后将引起不可严重的挽回的后果的情况</ac-view>
            </ac-view>
        </ac-view>

        <ac-view class="icon-box">
            <ac-icon :name="'time'" size="4rem" color="#00acff"></ac-icon>
            <ac-view class="icon-box-ctn">
                <ac-view class="icon-box-title">等待</ac-view>
                <ac-view class="icon-box-desc">用于表示等待，告知用户结果需等待</ac-view>
            </ac-view>
        </ac-view>

        <ac-view class="icon-box">
            <ac-icon :name="'checkoff'" size="1rem" color="#00acff"></ac-icon>
            <ac-view class="icon-box-ctn">
                <ac-view class="icon-box-title">多选控件图标_已选择</ac-view>
                <ac-view class="icon-box-desc">用于多选控件中，表示已选择该项目</ac-view>
            </ac-view>
        </ac-view>


        <ac-view class="icon-box">
            <ac-icon :name="'warn'" size="1rem" color="#f9615c"></ac-icon>
            <ac-view class="icon-box-ctn">
                <ac-view class="icon-box-title">错误提示</ac-view>
                <ac-view class="icon-box-desc">用于在表单中表示出现错误</ac-view>
            </ac-view>
        </ac-view>


    </ac-layout>
</template>

<script>
    import ACTopText from './ACTopText'

    export default {
        components: {ACTopText},
        config: {
            'navigationBarTitleText': 'icon'
        },
        name: 'Test',
        data() {
            return {
                title: 'icon',
                success: 'success',
                msg: 'test View'
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
    @import '../../common/app.less';

    .icon-box {
        margin-bottom: 2rem;
        padding: 0 3.75rem;
        display: flex;
        align-items: center;

    }

    .icon-box-img {
        margin-right: 2.3rem;
    }

    .icon-box-ctn {
        flex-shrink: 100;
    }

    .icon-box-title {
        font-size: 0.5rem;
    }

    .icon-box-desc {
        margin-top: 0.6rem;
        font-size: 0.5rem;
        color: #888;
    }

    .icon-custom-success {
        src: "success.png"
    }
</style>



