<template>
    <header ac:if="isHeaderShow" class="ac-navbar navbar-bottom-line-color" :class="classes" :style="{backgroundColor: getBackgroundColor(), height: height}">
        <!-- <div class="ac-navbar-item">
            <slot name="left"></slot>
        </div> -->

        <ac-navigator class="ac-navbar-item" openType="navigateBack"><slot name="left"></slot></ac-navigator>

        <div class="ac-navbar-center-box" :style="{height: height}">
            <div class="ac-navbar-center">
                <slot name="center"><span class="ac-navbar-center-title" :style="{color: getTextStyle(), fontSize: fontsize}">{{getTitle()}}</span></slot>
            </div>
        </div>
        <div class="ac-navbar-item">
            <slot name="right"></slot>
        </div>
    </header>
</template>

<script type="text/babel">
    import {isColor} from '../../../utils/assist';

    export default {
        methods: {

            isShowBackIcon(){
                return this.$customConfig !== undefined? this.$customConfig.navigationBarTitleText: this.$defConfig.window.navigationBarTitleText
            },
            getTitle() {
                return this.$customConfig !== undefined? this.$customConfig.navigationBarTitleText: this.$defConfig.window.navigationBarTitleText
            },
            getBackgroundColor(){
              let windownavigationBarBackgroundColor = this.$defConfig.window.navigationBarBackgroundColor
              if(this.$customConfig === undefined){
                return windownavigationBarBackgroundColor
              }
              let pagenavigationBarBackgroundColor = this.$customConfig.navigationBarBackgroundColor
              return this.isDefined(pagenavigationBarBackgroundColor)?pagenavigationBarBackgroundColor:windownavigationBarBackgroundColor
            },
            getTextStyle(){
              let windownavigationBarTextStyle;
              if(this.$defConfig.window.navigationBarTextStyle === 'black' || this.$defConfig.window.navigationBarTextStyle === 'white'){
                return this.$defConfig.window.navigationBarTextStyle
              }else{
                windownavigationBarTextStyle = 'black'
              }
              if(this.$customConfig === undefined){
                return windownavigationBarTextStyle
              }
              let pagenavigationBarTextStyle = this.$customConfig.navigationBarTextStyle
              if(pagenavigationBarTextStyle === 'black' || pagenavigationBarTextStyle === 'white'){
                return pagenavigationBarTextStyle
              }
                return windownavigationBarTextStyle

            },
            isDefined(param){
              if(typeof param === 'undefined')
              return false
              return true
            },
            isHeaderShow(){
              if(this.$customConfig === undefined){
                return true
              }
              if(this.$customConfig.navigationStyle === 'custom'){
                return false
              }
              return true
            }
        },
        name: 'ac-navbar',
        props: {
            fixed: Boolean,

            bgcolor: {
                validator(value) {
                    if(!value) return true;
                    return isColor(value);
                },
                default: '#FFF'
            },
            color: {
                validator(value) {
                    if(!value) return true;
                    return isColor(value);
                },
                default: '#5C5C5C'
            },
            fontsize: {
                validator(value) {
                    return /^(\.|\d+\.)?\d+(px|rem)$/.test(value);
                },
                default: '.8rem'
            },
            height: {
                validator(value) {
                    return /^(\.|\d+\.)?\d+(px|rem)$/.test(value);
                },
                default: '2.2rem'
            }
        },
        computed: {
            classes() {
                return this.fixed ? 'ac-navbar-fixed' : '';
            }
        }
    }
</script>

<style lang="less">
    @import '../../../styles/components/navbar.less';
</style>



