<template>
    <ac-layout style="height: 1000px;">
        <ac-button type="primary" @tap="pageScrollTo">滚动到指定位置</ac-button>
    </ac-layout>
</template>

<script>
  export default {
    config: {
      navigationBarTitleText: '滚动到指定位置'
    },
    name: "pageScrollTo",
    data() {
      return {}
    },
    methods: {
      pageScrollTo() {
        appcan.pageScrollTo(100, 1)
      },
    }
  }
</script>

<style lang="less" scoped>
</style>



