<template>
    <ac-layout>
        <ac-button @tap="defaultLoading" type="primary">基本使用</ac-button>
        <ac-button @tap="successLoading" type="primary">不显示取消项</ac-button>
        <ac-button @tap="customLoading" type="primary">选项内容为HTML</ac-button>
        <ac-button @tap="showText" type="primary">显示提示文字</ac-button>
    </ac-layout>

</template>

<script>
    var util = require('@/utils/util')
    var playTimeInterval
    var recordTimeInterval
    export default {
        config: {
            navigationBarTitleText: '动作面板'
        },
        data() {
            return {

            }
        },
        methods: {
          defaultLoading(){
            appcan.showActionSheet({
              itemList: [
                {
                  text: '选项1',
                  color: '#ff2f00'
                },
                {
                  text: '选项2',
                  color: '#b0e46e'
                }
              ],
              showCancel:true,
              success: (res) => {
                appcan.showToast({
                  title: `index: ${res.index}`
                })
              }
            })
          },
          successLoading(){
            appcan.showActionSheet({
              itemList: [
                {
                  text: '选项1',
                  color: '#ff2f00'
                },
                {
                  text: '选项2',
                  color: '#b0e46e'
                }
              ],
              showCancel: false,
              success: (index) => {
                appcan.showToast({
                  title: `index: ${index}`
                })
              }
            })
          },
          customLoading() {
            appcan.showActionSheet({
              itemList: [
                {
                  text: '分享到微博',
                  color: '#ff2f00'
                },
                {
                  text: '分享到微信',
                  color: '#b0e46e'
                }
              ],
              success: (index) => {
                appcan.showToast({
                  title: `index: ${index}`
                })
              }
            })
          },
          showText(){
            appcan.showActionSheet({
              header: '<p>确定吗?<br><span style="color:#666;font-size:12px;">删除后就无法撤消了哦</span></p>',
              itemList: [
                {
                  text: '删除',
                  color: 'red'
                }
              ],
              success: (index) => {
                appcan.showToast({
                  title: `index: ${index}`
                })
              }
            })
          }
        }
    }
</script>

<style scoped lang="less">

    .ac-actionsheet-menu-cell {
        font-size: 14px;
        color: #666;
    }
</style>



