

<template>
  <ac-layout>
    <ac-navbar ac:if="showNavbar" slot="top">
        <ac-navbar-back-icon slot="left" ac:if="showBack"></ac-navbar-back-icon>
    </ac-navbar>
    <router-view/>
    <!--<ac-tabbar slot="bottom" :tabindex.sync="activeIndex"></ac-tabbar>-->
    <ac-tabbar ac:if="showTabbar" slot="bottom" :tabindex.sync="activeIndex"></ac-tabbar>
  </ac-layout>
</template>
/* eslint-disable */
<script>

export default {
  name: 'App',
  data () {
    return {
      activeIndex:0
    }
  },
  props: {
      link: String,
  },
  watch:{
    '$route' (to, from) {
      // 对路由变化作出响应...

      if(!this.isApp && this.isTabPath(to.path)){
        this.$nextTick(m=>{
          this.activeIndex = this.findTab(to.path)
        })
      }
    }
  },
  computed:{
    isApp(){
      return appcan.IS_APP
    },
    // 控制底部标签栏是否显示
    isTabBar () {
      return this.$defConfig.tabbar && this.$defConfig.tabbar.list.length
    },
    nowIsTab(){
      const list = this.$defConfig.tabbar.list
      for(let i in list){
        if(list[i].pagePath === this.$route.path){
          return true
        }
      }
      return false
    },
    // 控制通用导航栏是否显示返回按钮（首页不显示，其他页显示）
    showBack () {
      return !this.nowIsTab
    },
    showNavbar(){
      return !this.isApp && this.$defConfig.window.navigationBarTitleText !== 'custom'
    },
    showTabbar(){
      return !this.isApp && this.nowIsTab
    }
  },
  mounted() {
    this.$nextTick(m=>{
      this.activeIndex = this.findTab(this.$router.currentRoute.path)
    })

  },
  methods: {
    isTabPath(path){
      const list = this.$defConfig.tabbar.list
      for(let i in list){
        if(list[i].pagePath === path){
          return true
        }
      }
      return false
    },
    findTab(path){
      const list = this.$defConfig.tabbar.list
      for(let i in list){
        if(list[i].pagePath === path){
          return i
        }
      }
      return -1
    }
  }
}
</script>





