<template>
    <ac-layout>
        <ACTopText :text="text"></ACTopText>

        <ac-audio :current="current"></ac-audio>
    </ac-layout>
</template>

<script type="text/babel">
    import ACTopText from './ACTopText'

    export default {
        config: {
            navigationBarTitleText: '音频'
        },
        components: {ACTopText},
        data() {
            return {
                text: 'audio',
                current: {
                  src: 'http://win.web.rc01.sycdn.kuwo.cn/6862d1fa101b5a7660b7e731929dfcde/5c257f93/resource/n2/25/79/3805662588.mp3',
                  name: '故事与她',
                  author: '沈念',
                },
            }
        },
    }
</script>
<style lang="less">


</style>



