<template>
    <ac-layout>
        <ac-block class="input-demo">
            <ac-input v-model="clientId"></ac-input>
        </ac-block>
        <ac-button @tap="getCode" type="primary">获取code</ac-button>

        <ac-block class="input-demo">
            <ac-input :disabled="true" v-model="applicationToken"></ac-input>
        </ac-block>
        <ac-button @tap="getToken" type="primary">获取access_token</ac-button>

        <ac-block class="input-demo">
            <ac-input :disabled="true" v-model="userInfo"></ac-input>

        </ac-block>

        <ac-text>{{userInfo}}</ac-text>
        <ac-button @tap="getUserInfo" type="primary">获取用户个人信息3</ac-button>
    </ac-layout>
</template>

<script type="text/babel">
  export default {
    config: {
      navigationBarTitleText: '微应用授权1'
    },
    data() {
      return {
        userInfo: '用户信息',
        applicationToken: 'access_token',
        applicationCode: 'code',
        clientId: 'fc8c6a215eee4618b6cf24bb6e328329',
      }
    },
    methods: {
      getCode(){
        let o = {
          scope:'snsapi_userinfo',
          clientId:this.clientId
        }
        appcan.loginAuth(o, (code)=> {
          this.applicationCode = code
          alert(this.applicationCode)
        });


        // appcan.request({
        //   url: 'http://192.168.131.4:9001/auth-api/code',
        //   method:'POST',
        //   data:{
        //     appId: "fc8c6a215eee4618b6cf24bb6e328329",
        //     authParams:{
        //       token: "eyJhbGciOiJSUzI1NiIsInR5cCI6IkpXVCJ9..e_VwdpIiOYFW861bzZspHY9L4oKHjkxoVIBEuwaqNIgf_9aU5vvTLGZQZ-9l5C5ggWwgHx9yVRF-N6E_4bxhfa_xLQbtNCbFode8BQizdkp7YfY2itN3YNkL4vLKy661peUlX7_XOJS03EKu9yoq86E9P60_VEVJFGjJ6jSmndkyYLEnkEhC8FatWjkm63Ct3MCCowOj9amdEFBT2cJekM0ZPrg5h3X1d82znHn0QKNXGAOQYfEWgnq2xnkFHBbY-crO-wdQtXZBX0R9TSD81n1mdvJR6mL4yviZ7rEpEAMUP1GMBAOmwtnhDR-fdEd3tnt2LIRrQuxVYySB9_0AsQ"
        //     },
        //     clientId: "fc8c6a215eee4618b6cf24bb6e328329",
        //     clientParams:{
        //       appPackageName: "com.xmgov.xmapp",
        //       appVersion: "1.0.1",
        //       carrierName: "中国联通",
        //       deviceId: "ffffffff-ed45-0115-ffff-ffff84c739ff",
        //       deviceType: "Xiaomi MI 5",
        //       network: "wifi",
        //       os: "Android",
        //       systemVersion: "6.0.1",
        //       timeStamp: 1540216505816
        //     },
        //     scope: "snsapi_userinfo"
        //   },
        //   header:{
        //     'Content-Type':'application/json'
        //   },
        //   success:  (result)=> {
        //     alert(result.data.content)
        //     this.applicationCode = result.data.content
        //   },
        //   fail:  ({errMsg})=> {
        //     console.log('request fail', errMsg)
        //   }
        // })
      },
      getToken() {
        appcan.request({
          url: 'http://192.168.15.78:9001/oauth/token',
          method:'POST',
          header:{
            'Content-Type':'application/x-www-form-urlencoded'
          },
          data:{
            code:this.applicationCode,
            client_secret:'6f22aaf33e504225bfd9feaa6f47e8c8',
            grant_type:'authorization_code',
            client_id:'fc8c6a215eee4618b6cf24bb6e328329'
          },
          success:  (result)=> {
            this.applicationToken = result.data.access_token
          },
          fail:  ({errMsg})=> {
            alert('request fail', errMsg)
          }
        })
      },
      getUserInfo() {
        appcan.request({
          url: 'http://192.168.15.78:9002/resource/user/userinfo',
          method:'POST',
          data: {
            access_token:this.applicationToken
          },
          header:{
            'Content-Type':'application/x-www-form-urlencoded'
          },
          success:  (result)=> {
            this.userInfo = result.data;
          },
          fail:  ({errMsg})=> {
            alert('request fail', errMsg)
          }
        })
      }
    }
  }
</script>
<style lang="less">
    .ac-content .win {
        padding: 10px;
        color: #999;
    }

    .input-demo {
        display: -webkit-flex;
        display: flex;
        -webkit-align-items: center;
        align-items: center;
        width: 100%;
        border-bottom: 1px solid #ccc;
        border-top: 1px solid #ccc;
        background-color: #fff;
        padding: 4px 12px;
    }

</style>



