<template>
    <div :style="{height: barHeight}">
        <footer class="ac-tabbar tabbbar-top-line-color" :class="classes" :style="styles" ref="navbar">
            <ac-tabbar-item ac:for="(item, index) in list" :key="index" :itemIndex="index" :title="item.text" :link="item.pagePath"
            :isCurrent=true @clickItem="setCurrent">
               <ac-icon :name="item.iconPath" slot="icon" size="1rem"></ac-icon>
            </ac-tabbar-item>
        </footer>
    </div>
</template>

<script type="text/babel">
    import {isColor} from '../../../utils/assist';

    export default {
        name: 'ac-tabbar',
        data() {
            return {
                activeIndex:0,
                barHeight: 'auto',
                color: this.$defConfig.tabbar.color,
                activeColor: this.$defConfig.tabbar.selectedColor,
                borderStyle: this.$defConfig.tabbar.borderStyle,
                backgroudColor: this.$defConfig.tabbar.backgroudColor,
                list: this.$defConfig.tabbar.list,
            }
        },
        props: {

            value: Number,
            fixed: Boolean,
            exact: {
                type: Boolean,
                default: true
            },
            activeClass: {
                type: String,
                default: 'router-link-active'
            },
            // activeColor: {
            //     validator(value) {
            //         if (!value) return true;
            //         return isColor(value);
            //     },
            //     default: '#09BB07'
            // },
            bgcolor: {
                validator(value) {
                    if (!value) return true;
                    return isColor(value);
                },
                default: '#FFF'
            },
            fontsize: {
                validator(value) {
                    return /^(\.|\d+\.)?\d+(px|rem)$/.test(value);
                },
                default: '.6rem'
            },
            tabindex: {
                default: 0
            },
        },
        computed: {
            classes() {
                return this.fixed ? 'ac-tabbar-fixed' : '';
            },
            styles() {
                return {
                    color: this.color,
                    backgroundColor: this.backgroudColor,
                    fontSize: this.fontsize
                }
            }
        },
        methods: {
            setCurrent(index) {
              this.activeIndex = index
                const tabPanels = this.$children.filter(item => item.$options.name === 'ac-tabbar-item');
                tabPanels.forEach((item, key) => {
                    this.$set(item, 'isCurrent', index === key);
                });
            },
            clickItem(index){
                console.log(index)
            }
        },
        watch: {
            tabindex(val) {
              this.activeIndex = parseInt(val)
              this.$emit('update:tabindex', this.activeIndex)
            },
            activeIndex(val) {
                this.setCurrent(val);
                this.$emit('update:tabindex', val)
            }
        },
        mounted() {
            this.setCurrent(this.activeIndex);

            if (this.fixed) {
                this.$nextTick(() => {
                    this.barHeight = this.$refs.navbar.offsetHeight;
                });
            }
        }
    }
</script>

<style lang="less" scoped>
    @import '../../../styles/components/tabbar.less';
</style>





