<template>
    <ac-layout>
        <ac-button type="primary" @tap="playVoice">playVoice</ac-button>
        <ac-button type="primary" @tap="pauseVoice">pauseVoice</ac-button>
        <ac-button type="primary" @tap="stopVoice">stopVoice</ac-button>
    </ac-layout>

</template>
<script>
    const util = require('@/utils/util')
    const formatLocation = util.formatLocation

    export default {
        config: {
            navigationBarTitleText: '音频播放'
        },
        data() {
            return {}
        },
        methods: {
            playVoice: function () {
                appcan.playVoice({
                    filePath: 'http://ws.stream.qqmusic.qq.com/M500001VfvsJ21xFqb.mp3?guid=ffffffff82def4af4b12b3cd9337d5e7&uin=346897220&vkey=6292F51E1E384E061FF02C31F716658E5C81F5594D561F2E88B854E81CAAB7806D5E4F103E55D33C16F3FAC506D1AB172DE8600B37E43FAD&fromtag=46',
                    complete: () => {

                    }
                })
            },
            pauseVoice: function () {
                appcan.pauseVoice()
            },
            stopVoice: function () {
                appcan.stopVoice()
            }
        },
        mounted() {
        }
    }
</script>



