<template>
  <div
    class="ac-view"
    :class="selfHoverClass"
    @touchstart="handleTouchStart"
    @touchmove="handleTouchMove"
    @touchend="handleTouchEnd"
    @touchcancel="handleTouchCancel"
    @tap="tap"
    @longtap="longtap"
    >
    <slot></slot>
    </div>
</template>

<script type="text/babel">
import mixinHover from "../../../ac/libs/mixin_hover";
export default {
  name: "ac-view",
  staticRenderFns: [],
  mixins: [mixinHover],
  methods:{
    tap(e){
      this.$emit('tap',e)
    },
    longtap(e){
      this.$emit('longtap',e)
    },
    click(e){
      this.$emit('click',e)
    }
  }
};
</script>

<style lang="less">
    @import "../../../styles/components/view.less";
</style>



