<template>
    <ac-layout>
        <ACTopText :text="title"></ACTopText>
        <ac-view>
            <ac-view class="page-body">
                <ac-view class="page-section page-section-gap" style="margin-left:20px;padding:10px 0;font-size:1rem;">
                    <ac-label class="checkbox">
                        <ac-checkbox value="cb" :checked="true"></ac-checkbox>
                        选中
                    </ac-label>
                    <ac-label class="checkbox">
                        <ac-checkbox value="cb"></ac-checkbox>
                        未选中
                    </ac-label>
                </ac-view>
            </ac-view>
        </ac-view>
    </ac-layout>
</template>

<script>
    import ACTopText from './ACTopText'

    export default {
        components: {ACTopText},
        config: {
            'navigationBarTitleText': 'checkbox'
        },
        data() {
            return {
                title: 'checkbox'
            }
        },
        methods: {
            formSubmit(e) {
                console.log(e)
            },
            formReset(e) {
            }
        }
    }

</script>

<style lang="less">
    @import '../../common/app.less';

    .checkbox {
        margin-right: 1rem;
        display: inline-block;
    }

</style>



