<template>
<ac-layout>
  <ACTopText :text="title"></ACTopText>
  <ac-view class="container">

    <ac-view class="page-body">
      <ac-view class="page-section page-section-spacing swiper">
        <ac-swiper :indicator-dots="indicatorDots" :autoplay="autoplay" :circular="false" :interval="interval" :duration="duration">
          <!-- autoplay circular interval="5000" duration="500" -->
          <!-- <ac-swiper bindchange="handleChange" autoplay circular interval="5000" duration="500"> -->
          <!-- <ac-block ac:for="(item, id) in background"> -->
          <ac-swiper-item ac:for="(item, id) in background" :key="id">
            <ac-view class="swiper-item" :class="item"></ac-view>
          </ac-swiper-item>
          <!-- </ac-block> -->
        </ac-swiper>
      </ac-view>

      <ac-view class="page-section" style="margin-top: 1rem;margin-bottom: 0;">
        <ac-view class="weui-cells weui-cells_after-title">
        <ac-view class="weui-cell weui-cell_switch">
          <ac-view class="weui-cell__bd">指示点</ac-view>
          <ac-view class="weui-cell__ft">
            <ac-switch v-model="indicatorDots"  @change="changeIndicatorDots" ></ac-switch>

          </ac-view>
        </ac-view>
        <ac-view class="weui-cell weui-cell_switch">
          <ac-view class="weui-cell__bd">自动播放</ac-view>
          <ac-view class="weui-cell__ft">
            <!-- <switch checked="{{autoplay}}" bindchange="changeAutoplay" /> -->
            <ac-switch v-model="autoplay"  @change="changeAutoplay" ></ac-switch>
          </ac-view>
        </ac-view>
      </ac-view>

      <ac-view class="page-section page-section-spacing">
      <ac-view class="page-section-title">
        <ac-text>幻灯片切换时长(ms)</ac-text>
        <!--<ac-text class="info">{{duration}}</ac-text>-->
      </ac-view>
      <ac-slider @change="durationChange" :show-value="true" :width="width" :value="duration" min="500" max="2000"/>
      <ac-view class="page-section-title">
        <ac-text>自动播放间隔时长(ms)</ac-text>
        <!--<ac-text class="info">{{interval}}</ac-text>-->
      </ac-view>
      <ac-slider @change="intervalChange" :show-value="true" :width="width" :value="interval" min="2000" max="10000"/>
    </ac-view>

    </ac-view>
    </ac-view>
  </ac-view>
</ac-layout>
</template>

<script>
  import ACTopText from './ACTopText'
  export default {
    components: {ACTopText},
    config: {
        navigationBarTitleText: "swiper基本使用"
    },
    data() {
      return {
        background: ['demo-text-1', 'demo-text-2', 'demo-text-3'],
        indicatorDots: true,
        vertical: false,
        autoplay: false,
        interval: 2000,
        duration: 500,
        title: 'swiper',
        width: window.innerWidth - window.rem*3
      }

    },
    methods: {
      changeIndicatorDots(index) {
        this.indicatorDots = !this.indicatorDots
      },
      changeAutoplay() {
        this.autoplay = !this.autoplay
      },
      intervalChange(e) {
        this.interval = e
      },
      durationChange(e) {
        this.duration = e
      }
    }
  }
</script>


<style scoped lang="less">
  .swiper-item {
    display: block;
    height: 300rpx;
  }

  .page-section-title{
    padding: 0;
  }

  .info{
    position: absolute;
    right: 0;
    color: #353535;
    font-size: 30/20rem;
  }
  .page-foot{
    margin-top: 50/20rem;
  }

  .page-section-title{
    margin-top: 3rem;
    position: relative;
  }
</style>
<style lang="less">
  @import '../../common/acui.less';

</style>



