<template>
  <ac-layout>
    <ac-button @tap="defaultLoading" type="primary">默认Loading</ac-button>
    <ac-button @tap="successLoading" type="primary">指定Loading文字</ac-button>
    <ac-button @tap="customLoading" type="primary">自定义背景</ac-button>
    <ac-button @tap="areLineBreaks" type="primary">自定义背景</ac-button>
  </ac-layout>
</template>

<script>
var util = require("@/utils/util");
var playTimeInterval;
var recordTimeInterval;
export default {
  config: {
    navigationBarTitleText: "加载框"
  },
  data() {
    return {};
  },
  methods: {
    hideLoading() {
      setTimeout(() => {
        appcan.hideLoading();
      }, 2000);
    },
    defaultLoading() {
      appcan.showLoading({ type: 1 });
      if (!appcan.IS_APP) {
        this.hideLoading();
      }
    },
    successLoading() {
      appcan.showLoading({
        title: "正在查询机构"
      });
       if (!appcan.IS_APP) {
        this.hideLoading();
      }
    },
    customLoading() {
      appcan.showLoading({
        title: "正在加载...",
        maskStyle: {
          "background-color": "rgba(231,231,231,0)"
        }
      });
       if (!appcan.IS_APP) {
        this.hideLoading();
      }
    },
    areLineBreaks() {
      appcan.showLoading({
        title: "仿支付宝...",
        type: 2,
        maskStyle: {
          "background-color": "rgba(231,231,231,0)",
          opacity: ".4",
          left: "50",
          top: "50",
          right: "50",
          bottom: "50"
        }
      });
       if (!appcan.IS_APP) {
        this.hideLoading();
      }
    }
  }
};
</script>

<style scoped lang="less">
</style>



